body {
  font-family: "Ubuntu", sans-serif;
  background: -webkit-linear-gradient(bottom, #232526, #414345, #232526);
  background: linear-gradient(to top, #232526, #414345, #232526);
  height: 100vh;
  color: red;
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  box-sizing: border-box;
}

.brand {
  width: 200px;
  height: 200px;
  position: relative;
}
.brand:before {
  background-size: 100% 100%;
  content: "\1F43C";
  line-height: 205px;
  color: #ff004e;
  font-size: 40px;
  font-family: "HEAVITAS", "Helvetica Rounded", Arial, sans-serif;
  font-weight: bold;
  text-align: center;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: absolute;

  z-index: 99999 !important;
}

.logo {
  -webkit-animation: 5s linear infinite;
          animation: 5s linear infinite;
  -webkit-animation-name: rotate-0;
          animation-name: rotate-0;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}

svg path {
  positon: absolute;
  fill: none;
  stroke-width: 2;
  top: 0;
  left: 0;
  /*mix-blend-mode: multiply;*/
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-animation: 4s linear infinite;
          animation: 4s linear infinite;
}

.logo svg path:nth-child(1) {
  opacity: 1;
  -webkit-animation-name: rotate-1;
          animation-name: rotate-1;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}
.logo svg path:nth-child(2) {
  opacity: 0.5;
  -webkit-animation-name: rotate-2;
          animation-name: rotate-2;
  -webkit-animation-delay: 0.6s;
          animation-delay: 0.6s;
}
.logo svg path:nth-child(3) {
  opacity: 0.8;
  -webkit-animation-name: rotate-3;
          animation-name: rotate-3;
  -webkit-animation-delay: 0.7s;
          animation-delay: 0.7s;
}

@-webkit-keyframes scale-1 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes scale-1 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes rotate-0 {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate-0 {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes rotate-1 {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate-1 {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes rotate-2 {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@keyframes rotate-2 {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@-webkit-keyframes rotate-3 {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes rotate-3 {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

